import { BlogPost } from '@nx-gatsby-blogs/ui';

export interface BlogPostArchivePropsData {
  allWpPost: {
    nodes: {
      author: {
        node: {
          firstName: string;
          lastName: string;
          name: string;
          uri: string;
          avatar: {
            url: string;
          };
        };
      };
      id: string;
      title: string;
      excerpt: string;
      date: string;
      featuredImage?: {
        node: {
          id: string;
          link: string;
          altText: string;
        };
      };
    }[];
  };
}

console.log(process.env.NX_WORDPRESS_SITE_URL, process.env);

const WordpressSiteUrl = process.env.NX_WORDPRESS_SITE_URL;

export function transformBlogPostArchivePropsDataToBlogPosts(
  data: BlogPostArchivePropsData
): BlogPost[] {
  return data?.allWpPost?.nodes?.map((node) => {
    const author = node.author?.node;
    return {
      author: author && {
        name: author.firstName + ' ' + author.lastName,
        username: author.name,
        avatar: author.avatar.url,
        url: WordpressSiteUrl + author.uri,
      },
      id: node.id,
      title: node.title,
      excerpt: node.excerpt,
      date: node.date,
      featuredImage: {
        src: node.featuredImage?.node?.link,
        alt: node.featuredImage?.node?.altText,
      },
    };
  });
}
